import FilterBtn from "../../UI/FilterBtn";
import Section from "../../UI/Section";
import SectionHdr from "../../UI/SectionHdr";
import FilterModal from "./FilterModal";
import styles from "./Graph.module.css";
import ReactDOM from "react-dom";
import { useContext, useState } from "react";

import {
  generateXArray,
  generateXAxis,
  generateXLabel,
  generateYAxis,
  generateYLabel,
  generateGraphData,
  getYMax,
  removeDuplicates,
  timerConverter,
  dataSorter,
  generateFakeData,
} from "./GraphHelper";
import GraphWrapper from "./GraphWrapper";
import GraphContext from "./GraphContext";

const Graph = (props) => {
  const graphCtx = useContext(GraphContext);
  /**
   * Props Required:
   * data: DATASET
   * xAxis sort: ascending / descending
   * xAxsis parameter: day / time
   * xLabeltext
   * yLabeltext
   * markerHillview:
   * markerBeautyWorld:
   */

  const [toggleModal, setToggleModal] = useState(false);

  const DATA_CONVERTED = timerConverter(graphCtx.graphData.xAxis, props.data);
  const DATA_SORTED = dataSorter(DATA_CONVERTED, graphCtx.graphData.sort);

  const yMax = getYMax(DATA_SORTED);
  const yAxis = generateYAxis(yMax, styles.yAxis);

  const xArray = generateXArray(DATA_SORTED);
  const xArrayUnique = removeDuplicates(xArray);
  const xMax = xArrayUnique.length;

  const xAxis = generateXAxis(xArrayUnique, yMax, styles.xAxis);

  const xLabelText = graphCtx.graphData.xAxis === "day" ? "Day of the Month" : "Time of Day";
  const yLabelText = "Time taken to reach home / mins";

  const xLabel = generateXLabel(yMax, xMax, xLabelText);
  const yLabel = generateYLabel(styles.yLabel, yMax, yLabelText);

  const graphData = generateGraphData(
    DATA_SORTED,
    yMax,
    xArrayUnique,
    graphCtx.graphData.markerH,
    graphCtx.graphData.markerB
  );

  function onFilterClick() {
    setToggleModal((previousState) => !previousState);
  }

  return (
    <GraphWrapper size="flex">
      <Section>
        <SectionHdr>Graph</SectionHdr>
        <FilterBtn onFilterClick={onFilterClick} />
        {toggleModal &&
          ReactDOM.createPortal(
            <FilterModal onFilterClick={onFilterClick} />,
            document.querySelector("#modal-content")
          )}
      </Section>

      <div
        style={{
          gridTemplateRows: `repeat(${yMax + 2}, 1fr)`,
          gridTemplateColumns: ` 20px repeat(${xArrayUnique.length + 1}, 1fr)`,
        }}
        className={styles.graph}
      >
        {graphData}
        {yAxis}
        {xAxis}
        {xLabel}
        {yLabel}
      </div>
    </GraphWrapper>
  );
};

export default Graph;
