import { useContext, useEffect } from "react";
import { dataSorter } from "../Analytics/Graph/GraphHelper";
import HorbContext from "../store/HorbContext";
import Section from "../UI/Section";
import SectionHdr from "../UI/SectionHdr";
import LogItem from "./LogItem";
import styles from "./LogList.module.css";

const LogList = () => {
  const HorbCtx = useContext(HorbContext);
  const routeArray = dataSorter(HorbCtx.routeList, "descending").map((ele) => {
    const processedDate = new Date(ele.time);
    const newDuration = ele.duration
      .split(":")
      .map((ele, i) => (i == "0" && ele[0] == "0" ? ele.slice(1) : ele))
      .join(":");
    return (
      <LogItem
        name={ele.name}
        profile={ele.name.toLowerCase().replace(" ", "")}
        route={ele.route}
        duration={newDuration}
        time={`${processedDate.getHours().toString().padStart(2, "0")}:${processedDate
          .getMinutes()
          .toString()
          .padStart(2, "0")}`}
        date={`${processedDate.getDate()}/${
          processedDate.getMonth() + 1
        }/${processedDate.getFullYear()}`}
      />
    );
  });

  return (
    <div>
      <Section>
        <SectionHdr type="page">Logs</SectionHdr>
      </Section>
      <ul className={styles.list}>{routeArray}</ul>
    </div>
  );
};

export default LogList;
