import { useReducer } from "react";
import GraphContext from "./GraphContext";

const graphReducer = function (state, action) {
  if (action.type === "XAXIS_CHANGE") {
    return { ...state, xAxis: action.value };
  }
  if (action.type === "SORT_CHANGE") {
    return { ...state, sort: action.value };
  }
  if (action.type === "MARKERH_CHANGE") {
    return { ...state, markerH: action.value };
  }
  if (action.type === "MARKERB_CHANGE") {
    return { ...state, markerB: action.value };
  }
};

const GraphContextProvider = (props) => {
  const [graphData, dispatchGraph] = useReducer(graphReducer, {
    xAxis: "day",
    sort: "ascending",
    markerH: "🚆",
    markerB: "🚍",
  });

  function onXChange(input) {
    dispatchGraph({ type: "XAXIS_CHANGE", value: input });
  }
  function onSortChange(input) {
    dispatchGraph({ type: "SORT_CHANGE", value: input });
  }
  function onMarkerHChange(input) {
    dispatchGraph({ type: "MARKERH_CHANGE", value: input });
  }
  function onMarkerBChange(input) {
    dispatchGraph({ type: "MARKERB_CHANGE", value: input });
  }

  return (
    <GraphContext.Provider
      value={{ onXChange, onSortChange, onMarkerHChange, onMarkerBChange, graphData }}
    >
      {props.children}
    </GraphContext.Provider>
  );
};

export default GraphContextProvider;
