import HorbContext from "./HorbContext";
import { useState } from "react";

const HorbContextProvider = (props) => {
  const [routeList, setRouteList] = useState(false);
  const [tabCurrent, setTabCurrent] = useState({
    form: false,
    analytics: true,
    logs: false,
  });
  async function sendRouteData(inputData) {
    const response = await fetch(
      "https://horb-32823-default-rtdb.asia-southeast1.firebasedatabase.app/route.json",
      {
        method: "POST",
        body: JSON.stringify(inputData),
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    const data = await response.json();
    console.log(data);
  }

  async function getRouteList(inputData) {
    const response = await fetch(
      "https://horb-32823-default-rtdb.asia-southeast1.firebasedatabase.app/route.json"
    );
    const data = await response.json();
    console.log(data);
    await setRouteList(Object.values(data));
  }

  function onLogsClick() {
    setTabCurrent({
      form: false,
      analytics: false,
      logs: true,
    });
  }
  function onFormClick() {
    setTabCurrent({
      form: true,
      analytics: false,
      logs: false,
    });
  }
  function onAnalyticsClick() {
    setTabCurrent({
      form: false,
      analytics: true,
      logs: false,
    });
  }

  return (
    <HorbContext.Provider
      value={{
        sendRouteData,
        getRouteList,
        onLogsClick,
        onFormClick,
        onAnalyticsClick,
        routeList,
        tabCurrent,
      }}
    >
      {props.children}
    </HorbContext.Provider>
  );
};

export default HorbContextProvider;
