import styles from "./NavItem.module.css";
import Icons from "../Assets/Icons";
import { useContext, useEffect } from "react";
import HorbContext from "../store/HorbContext";

const NavItem = (props) => {
  const HorbCtx = useContext(HorbContext);

  function onButtonClick() {
    props.onNavClickHandler();
  }
  return (
    <button
      onClick={onButtonClick}
      className={`${styles["nav__item"]} ${styles[props.variation]} ${
        HorbCtx.tabCurrent[props.name] ? styles.selected : ""
      }`}
    >
      {Icons[props.name]}
    </button>
  );
};

export default NavItem;
