import { useContext } from "react";
import HorbContext from "../../store/HorbContext";
import Chip from "../../UI/Chip";
import Number from "../../UI/Number";
import styles from "./AverageItem.module.css";

function AverageItem(props) {
  const HorbCtx = useContext(HorbContext);
  function generateAverage() {
    return HorbCtx.routeList
      .reduce((acc, ele, i, arr) => {
        let calcualtedMin = 0;
        if (ele.route.toLowerCase() === props.name) {
          const durationArr = ele.duration.split(":");
          const durationMin = parseInt(durationArr[0]) + parseInt(durationArr[1]) / 60;
          calcualtedMin = durationMin / arr.length;
          console.log(durationArr, durationMin, calcualtedMin, arr.length, acc);
        }
        return acc + calcualtedMin;
      }, 0)
      .toFixed(2);
  }

  return (
    <div className={styles["average__item"]}>
      <div className={styles["average__info"]}>
        <Chip size="normal" position="absolute">
          {props.name}
        </Chip>
      </div>
      <Number value={generateAverage()} unit="min" />
    </div>
  );
}

export default AverageItem;
