import Form from "./Form/Form";
import LogList from "./Log/LogList";
import Button from "./UI/Button";
import HorbContext from "./store/HorbContext";
import { useContext, useState, useEffect } from "react";
import Analytics from "./Analytics/Analytics";
import styles from "./Main.module.css";

const Main = () => {
  const HorbCtx = useContext(HorbContext);

  //   if (horbCtx.routeList) {

  //   }

  useEffect(() => {
    HorbCtx.getRouteList();
    console.log("Data fetched!");
  }, []);

  return (
    <div className={styles.main}>
      {HorbCtx.tabCurrent.form && <Form />}
      {HorbCtx.tabCurrent.logs && HorbCtx.routeList && <LogList />}
      {HorbCtx.tabCurrent.analytics && <Analytics />}
    </div>
  );
};

export default Main;
