import Button from "../UI/Button";
import Modal from "../UI/Modal";
import styles from "./FormModal.module.css";

const FormModal = (props) => {
  const icon = (
    <svg
      data-v-73918be8=""
      data-v-3c6e83d0=""
      viewBox="0 0 24 24"
      xmlns="http://www.w3.org/2000/svg"
      class=""
    >
      <path
        data-v-73918be8=""
        d="M10,17L5,12L6.41,10.58L10,14.17L17.59,6.58L19,8M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2Z"
      ></path>
    </svg>
  );
  return (
    <Modal className={styles["modal"]}>
      <div className={styles["modal__icon"]}>{icon}</div>
      <div className={styles["modal__title"]}>Success!</div>
      <div className={styles["modal__description"]}>
        Your log has been submitted successfully! Check the logs tab to see your entry.
      </div>
      <Button onClickHandler={props.onCloseClick} size="normal" color="gray">
        Close
      </Button>
    </Modal>
  );
};

export default FormModal;
