import Section from "../../UI/Section";
import SectionHdr from "../../UI/SectionHdr";
import styles from "./Average.module.css";
import AverageItem from "./AverageItem";

const Average = () => {
  return (
    <div className={styles.average}>
      <Section>
        <SectionHdr>Average</SectionHdr>
      </Section>
      <div className={styles["average__group"]}>
        <AverageItem name="hillview" />
        <AverageItem name="beauty world" />
      </div>
    </div>
  );
};

export default Average;
