import Button from "../../UI/Button";
import Modal from "../../UI/Modal";
import SectionHdr from "../../UI/SectionHdr";
import Selector from "../../UI/Selector";
import styles from "./FilterModal.module.css";
import { useContext } from "react";
import GraphContext from "./GraphContext";

const FilterModal = (props) => {
  const graphCtx = useContext(GraphContext);

  return (
    <Modal className={styles["filter__modal"]}>
      <SectionHdr>Filter</SectionHdr>
      <Selector
        onSelectorHandler={graphCtx.onXChange}
        justify="space-between"
        value={graphCtx.graphData.xAxis}
        id="X-Axis"
        label="X-Axis"
        name="X-Axis"
        options={["day", "time"]}
      />
      <Selector
        onSelectorHandler={graphCtx.onSortChange}
        justify="space-between"
        value={graphCtx.graphData.sort}
        id="sort"
        label="Sort"
        name="sort"
        options={["ascending", "descending"]}
      />
      <Selector
        onSelectorHandler={graphCtx.onMarkerHChange}
        justify="space-between"
        value={graphCtx.graphData.markerH}
        id="Marker (H)"
        label="Marker (H)"
        name="Marker (H)"
        options={["🚄", "🚆", "🚅", "🚂", "🚃", "🚉", "🛤", "👟"]}
      />
      <Selector
        onSelectorHandler={graphCtx.onMarkerBChange}
        justify="space-between"
        value={graphCtx.graphData.markerB}
        id="Marker (B)"
        label="Marker (B)"
        name="Marker (B)"
        options={["🚌", "🚐", "🚎", "🚍"]}
      />
      <div className={styles.controls}>
        <Button onClickHandler={props.onFilterClick} size="normal" color="gray">
          Close
        </Button>
      </div>
    </Modal>
  );
};

export default FilterModal;
