import styles from "./LogItem.module.css";
import yihein from "../Assets/profile/yihein.jpg";
import yikang from "../Assets/profile/yikang.jpg";
import yixuen from "../Assets/profile/yixuen.jpg";
import Chip from "../UI/Chip";
import Number from "../UI/Number";

const LogItem = (props) => {
  const images = { yihein, yikang, yixuen };
  return (
    <li className={styles["log__item"]}>
      <figure className={styles["log__figure"]}>
        <img src={images[props.profile]}></img>
      </figure>
      <div className={styles["log__details"]}>
        <div className={styles["log__name"]}>
          <h3>{props.name}</h3>
        </div>
        <div className={styles["log__chips"]}>
          <Chip size="small">{props.route}</Chip>
          <Chip size="small">{props.time}hrs</Chip>
          <Chip size="small">{props.date}</Chip>
        </div>
        <div className={styles["log__time"]}>
          <Number value={props.duration} unit="min" />
        </div>
      </div>
    </li>
  );
};

export default LogItem;
