import { useContext } from "react";
import HorbContext from "../store/HorbContext";
import styles from "./Nav.module.css";
import NavItem from "./NavItem";

const Nav = () => {
  const HorbCtx = useContext(HorbContext);
  return (
    <ul className={styles.nav}>
      <NavItem onNavClickHandler={HorbCtx.onLogsClick} variation="normal" name="logs" />
      <NavItem onNavClickHandler={HorbCtx.onFormClick} variation="pop" name="form" />
      <NavItem onNavClickHandler={HorbCtx.onAnalyticsClick} variation="normal" name="analytics" />
    </ul>
  );
};

export default Nav;
