import Input from "../UI/Input";
import Selector from "../UI/Selector";
import styles from "./Form.module.css";
import { useState, useContext } from "react";
import HorbContext from "../store/HorbContext";
import Button from "../UI/Button";
import Section from "../UI/Section";
import SectionHdr from "../UI/SectionHdr";
import FormModal from "./FormModal";

const Form = (props) => {
  const HorbCtx = useContext(HorbContext);
  const [namevalue, setNamevalue] = useState("");
  const [routevalue, setRoutevalue] = useState("");
  const [minvalue, setMinvalue] = useState(null);
  const [secvalue, setSecvalue] = useState(null);
  const [dateEntry, setDateEntry] = useState(Date.now());
  const [toggleForm, setToggleForm] = useState(false);

  console.log(namevalue, routevalue, minvalue, secvalue, dateEntry);

  async function onFormSubmitHandler(event) {
    event.preventDefault();
    await HorbCtx.sendRouteData({
      name: namevalue,
      route: routevalue,
      duration: `${minvalue}:${secvalue}`,
      time: dateEntry ? dateEntry : Date.now(),
    });
    await HorbCtx.getRouteList();
    setToggleForm((prevState) => !prevState);
  }

  function onDateEntryHandler(input) {
    setDateEntry(new Date(input).getTime());
  }

  function onCloseClick() {
    setToggleForm((prevState) => !prevState);
  }

  return (
    <div className={styles["form__wrapper"]}>
      {toggleForm && <FormModal onCloseClick={onCloseClick} />}
      <form onSubmit={onFormSubmitHandler} className={styles.form}>
        <Section>
          <SectionHdr>New Entry</SectionHdr>
        </Section>
        <Selector
          onSelectorHandler={setNamevalue}
          name="name"
          id="name-input"
          label="Name"
          options={["Yi Hein", "Yi Xuen", "Yi Kang"]}
        ></Selector>
        <Selector
          onSelectorHandler={setRoutevalue}
          name="Route"
          id="route-input"
          label="Route"
          options={["HillView", "Beauty World"]}
        ></Selector>
        <div className={styles.time}>
          <label htmlFor="min">
            <h4>Time</h4>
          </label>
          <Input
            onInputChange={setMinvalue}
            pattern="\d*"
            type="number"
            id="min"
            placeholder="min"
            size="small"
          />
          <Input
            onInputChange={setSecvalue}
            type="number"
            pattern="\d*"
            id="sec"
            placeholder="sec"
            size="small"
          />
        </div>
        <div className={styles.time}>
          <label htmlFor="date">
            <h4>Date</h4>
          </label>
          <Input
            onInputChange={onDateEntryHandler}
            type="date"
            id="date"
            placeholder="dd/mm/yyyy"
            size="normal"
          />
        </div>
        <Button size="normal" color="blue" type="submit">
          Submit
        </Button>
      </form>
    </div>
  );
};

export default Form;
