export function getYMax(data) {
  return Math.max(...data.map((ele) => +ele.duration));
}

export function generateYAxis(yMax, className) {
  return Array.from({ length: yMax + 1 }, (_, i) => {
    return (
      <div className={className} style={{ gridRow: `${yMax + 1 - i}`, gridColumn: 2 }}>
        {i}
      </div>
    );
  });
}

export function timerConverter(format = "day", data) {
  if (!format) return;
  if (format === "day") {
    return data.map((ele) => {
      const modifiedTime = new Date(ele.time).getDate();
      return { ...ele, time: modifiedTime, duration: +ele.duration.slice(0, 2) };
    });
  }
  if (format === "time") {
    return data.map((ele) => {
      const modifiedTime = new Date(ele.time).getHours();
      return { ...ele, time: modifiedTime, duration: +ele.duration.slice(0, 2) };
    });
  }
}

export function dataSorter(data, type) {
  if (type === "ascending") return data.sort((a, b) => a.time - b.time);
  if (type === "descending") return data.sort((a, b) => b.time - a.time);
}

export function generateXArray(data) {
  return data.map((ele) => ele.time);
}

export function removeDuplicates(array) {
  return [...new Set(array)];
}

export function generateXAxis(array, yMax, className) {
  return array.map((ele, i) => (
    <div
      key={getRand(10000, 99999)}
      className={className}
      style={{ gridRow: yMax + 1, gridColumn: i + 3 }}
    >
      {ele}
    </div>
  ));
}

export function generateXLabel(yMax, xMax, text) {
  return (
    <div
      key={getRand(10000, 99999)}
      style={{ gridArea: `${yMax + 2} / 2 / ${yMax + 3} / ${xMax + 3}` }}
    >
      <h6>{text}</h6>
    </div>
  );
}

export function generateYLabel(className, yMax, text) {
  return (
    <div
      key={getRand(10000, 99999)}
      className={className}
      style={{ gridArea: `1/1/${yMax + 3} / 2` }}
    >
      <h6>{text}</h6>
    </div>
  );
}

export function generateGraphData(data, yMax, array, markermrt, markerbus) {
  return data.map((ele, i) => {
    const xStart = array.findIndex((dd) => dd == ele.time) + 3;
    const yStart = yMax - ele.duration + 1;
    if (isNaN(+yStart)) console.log(`I failed: index - ${i}`);
    return (
      <div
        key={getRand(10000, 99999)}
        style={{ gridArea: `${yStart}/${xStart}/${yStart + 1}/${xStart + 1}` }}
      >
        {ele.route === "hillview" ? markermrt : markerbus}
      </div>
    );
  });
}

export function generateFakeData(datasetCount) {
  const fakeData = {
    route: {},
  };

  let insertionEntries = {};
  let i;
  const routes = ["hillview", "beauty world"];
  for (i = 0; i < datasetCount; i++) {
    insertionEntries[Math.random().toFixed(8).toString().replace(".", "")] = {
      duration: `${getRand(1, 25).toString().padStart(2, "0")}:${Math.floor(
        Math.random() * 10
      )}${Math.floor(Math.random() * 10)}`,
      name: "Yi Hein",
      route: routes[Math.round(Math.random())],
      time: +`1620${Math.floor(Math.random() * 10)}${Math.floor(Math.random() * 10)}${Math.floor(
        Math.random() * 10
      )}000000`,
    };
  }

  fakeData["route"] = insertionEntries;

  return fakeData;
}

function getRand(min, max) {
  return Math.floor(Math.random() * (max - min) + min);
}
